.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1300;
}

.modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: whitesmoke;
    padding: 1rem 1.5rem;
    width: 24rem;
    border-radius: 0.5rem;
    /* border: 1px solid black; */
}

.column {
    display: flex;
    flex-direction: column;
}

.changePassword {
    margin-top: 0;
    border: none;
    max-width: 330px;

}

.changePasswordHeading {
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 0.4rem;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
}

.changePassword_label {
    text-align: left;
    margin-bottom: 0.4rem;
}

.changePassword_field {
    padding: 0.5rem 1.5rem;
    border-radius: 0.5rem;
    margin-bottom: 0.4rem;
    outline: none;
}

.changePasswordBtn{
    margin-top: 0.6rem;
    padding: 0.5rem 2.5rem;
    background:white;
    border-radius: 0.5rem;
    font-size: 1rem;
    margin-bottom: 0.3rem;
}

.err_msg{
    color: red;
    font-size: 0.7rem;
    text-align: left;
    margin: 0;
}



.editProfile_form{
    margin: 0 auto;
    margin-top: 5rem;
    border: 1px solid black;
    max-width: 300px;
    padding: 2.4rem;
    border-radius: 0.5rem;
    /* box-shadow: 8px 8px 8px #888888; */
}

.editProfile_heading{
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 0.4rem;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
}

.editProfile_label{
    text-align: left;
    margin-bottom:0.4rem ;
}

.editProfile_field{
    padding: 0.5rem 1.5rem;
    border-radius: 0.5rem;
    margin-bottom:0.4rem ;
    outline: none;
}

.editProfile_btn{
    margin-top: 1rem;
    padding: 0.5rem 2.5rem;
    background:white;
    border-radius: 0.5rem;
    font-size: 1rem;
    /* margin-bottom: 0.2rem; */
}

