.product-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
}

.filter{
    position: fixed;
    width: 80px;
    height: 80px;
    cursor: pointer;
    font-size: 3.5rem;
    font-weight: 200;
    background:#232F3E;
    border-radius: 50%;
    right: 40px;
    bottom: 40px;
    box-shadow: 2px 2px 3px #999;
    color: whitesmoke;
    z-index: 1200;
    transition: box-shadow 0.2s ease-in-out;
    text-align: center;

}


@media(min-width:600px){    
    .product-container{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        /* justify-content: flex-start;*/
        grid-gap:1.6rem;
        gap:1.6rem; 
        /* justify-content: space-between; */
    }       
    
}

.filter:hover{
    box-shadow: 0 6px 14px 0 #666;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  

}

@media(max-width:600px){
    .filter{
        right:15px;
        width:70px;
        height: 70px;
        text-align: center; 
        bottom: 60px;
    }       
    
    .funnel{
        font-size: 2.7rem;
        color: whitesmoke;
    }
}



.img{
    max-width: 100%;
    height: auto;
}

.card {
    max-width: 250px;
    width: 250px;
    border-radius: 0.5rem;
    box-shadow: 8px 8px 8px #888888;
}

.card + .card {
    margin-top: 1rem;
}

.prod-img{
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-radius: 0.5rem 0.5rem 0rem 0rem;
}
.product-image{
    position: relative;
}
.product-details{
    text-align: left;
    margin-left:1rem;
}

.heart{
    position: absolute;
    right: 8px;
    top: 4px;
    z-index: 2;
    font-size: 1.5rem;
    cursor: pointer;
}

.product-name{
    /* margin: 0 auto; */
    width: 100%;
    font-size : 1.2rem;
    font-weight: 700;
    text-transform: uppercase;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.product-discount{
    font-size: 0.95rem;
    color: grey;
}

.product-price{
    font-size: 1.1rem;
    font-weight: 600;
}

.card-action{
    padding: 0.4rem;
}

.btn-shopping{
    /* background-color:grey;  */
    background-color:#232F3E; 
    border: none;
    color: white;
    padding: 15px 4.9rem;
    text-align: center;
    text-decoration: none;
    font-size: 1.2rem;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: .5rem;
    font-weight: 400;
    transition-duration: 0.4s;
    outline: none;
}

.shop{
    font-size: 1.2rem;
    margin-right: 0.5rem;
}

.btn-shopping:hover {
    background-color:#303e4f;
    color:white;
}

@media(min-width:600px){
    .card + .card {
        margin-top: 0;
        /* margin-left: 2rem; */
    }
    .card{
        margin-bottom: 2rem ;
    }
}
.loader{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 999;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    min-height: 100vh
   }

   .hide{
      display:"none"
   }


.row{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 0.8rem;
}

.filter_modal{
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    text-align: left;
}

.filter_content{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color:#fafafa;
    padding: 1rem 1.5rem;
    width: 24rem;
    border-radius: 0.5rem;
    border: 1px solid black;

}

.filter_header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}



.labels{
    margin-top: 0.6rem;
}

.brands{
    width: 25%;
}

.filter_close{
    display: none;
}

@media(max-width:600px){
    .filter_content{
        width: 21rem;
    }   
}
