.img {
    max-width: 100%;
    height: auto;
}

.product_desc {
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
    
    /* border: 1px solid black; */
}

.product_image {
    width: 40%;
}

.product_img {
    width: 100%;
    height: 400px;
    object-fit: cover;
}


.product_info {
    text-align: left;
    width: 55%;
    display: flex;
    flex-direction: column;
}

.product_brand {
    display: block;
    font-size: 1.4rem;
    font-weight: 500;
    letter-spacing: 0.05rem;
    margin-bottom: 0;
}

.product_name {
    display: block;
    font-size: 1.1rem;
    margin-bottom: 0.5rem;
    margin-top: 0px;
    font-weight: 300;
}

.product_price {
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 0.05rem;
    margin-right: 0.1rem;
}

.prod_desc{
    width: 80%;
    margin-top: 0.5rem;
    margin-bottom: 1.5rem;
}

/* .action_btn{
    /* border: 1px solid black; *
} */

.cart_btn{
    padding: 0.8rem 2rem;
    margin-right: 1.8rem;
    background-color:#232F3E; 
    border: none;
    text-align: center;
    text-decoration: none;
    font-size: 1rem;
    /* margin: 4px 2px; */
    cursor: pointer;
    border-radius: .5rem;
    font-weight: 400;
    color: white;
}

.wishlist_btn{
    padding: 0.8rem 1rem;
    background-color:#232F3E; 
    border: none;
    text-align: center;
    text-decoration: none;
    font-size: 1rem;
    /* margin: 4px 2px; */
    cursor: pointer;
    border-radius: .5rem;
    font-weight: 400;
    color: white;
}

@media(max-width:600px) {
    .product_desc {
        flex-direction: column;
        justify-content: none;
    }

    .product_image {
        width: 100%;
    }
    .product_img {
        width: 100%;
        /* height: 400px; */
        height: auto;
        object-fit: contain;
    }

    .product_info {
        text-align: left;
        width:100%;
    }
    .prod_desc{
        width: 100%;
    }

    .cart_btn{
        padding: 0.8rem 1rem;
        margin-right:1.2rem ;
    }
}


